.services{
	margin-top: 60px;
	text-align: center;
	text-transform: uppercase;
	color: $yellow;
	line-height: 30px;
	img{
		width: 85px;
		height: 85px;
	}
	h3 {
		font-size: $h3;
		letter-spacing: 1.6px;
		a{
			font-family: "Montserrat", sans-serif;
			color: $yellow;
			text-decoration: underline;
			transition: 0.5s ease-in-out;
			&:hover{
				text-decoration: none;
				background-color: $yellow;
				color: $primary-color;
			}
		}
	}

	.service-inner{
		h3{
			font-weight: 700;
			margin-bottom: 25px;
		}
		p{
			font-weight: 500;
			margin-bottom: 35px;
		}
		ul{
			margin: 0;
			padding: 0;
			li{
				list-style-type: none;
				display: inline-block;
			    margin: 0px 20px;
			    span{
			    	display: block;
			    	letter-spacing: 1px;
			    	margin-top: 10px;
			    	font-size: $span_small;
			    }
			}
		}
		background: #1A1616;
    	padding: 50px 0px;
    	margin-top: 80px;
	}
}