/* Tablet Layout */
@media only screen and (min-width: 768px) and (max-width: 991px) {
  h1 {
    font-size: 35px;
  }
  h2 {
    font-size: 28px !important;
  }
  h4 {
    font-size: 25px;
  }
  p {
    font-size: 16px !important;
  }
  .menu > div > div > ul > li > a {
    font-size: 16px;
  }

  .left-push {
    margin-left: 0% !important;
  }

  // Portfolio Section
  .grid-item,
  .grid-sizer {
    width: 50%;
  }

  .grid-col {
    padding: 0 0.5em;
  }

  .hero-portfolio {
    padding: 50px 0px 15px 0px;
  }

  .filter {
    ul {
      li {
        font-size: 14px;
        padding: 2px 3px;
      }
    }
  }

  // Single Portfolio

  // Blog Section
  

  // Single Blog
  .hero-single-blog {
    height: 90vh;
  }

  .blog-inner-heading {
    top: 130%;
  }
  .blog-inner-heading h1 {
    padding: 20px 0px;
  }
  .inner-single-blog-shadow {
    top: 67%;
    .ml-auto {
      margin-left: inherit;
    }
  }
}

/* Mobile Large Layout */
@media only screen and (min-width: 425px) and (max-width: 767px) {
